:root {
  font-size: 10px;
  --gradient-1: linear-gradient(
    170deg,
    rgba(249, 104, 103, 1) 0%,
    rgba(203, 91, 141, 1) 70%,
    rgba(136, 93, 151, 1) 100%
  );
  --gradient-2: linear-gradient(
    170deg,
    rgba(255, 146, 46, 1) 0%,
    rgba(249, 104, 103, 1) 70%,
    rgba(203, 91, 141, 1) 100%
  );
  color: white;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
}

.project-container {
  padding: 2em;
  border-radius: 1em;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 3em;
  width: 60%;
  min-width: 30em;
}

.project-container > :nth-child(2n - 1) {
  background: var(--gradient-1);
}

.project-container > :nth-child(2n) {
  background: var(--gradient-2);
}

.project {
  padding: 2em;
  border-radius: 1em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.ressource-container {
  display: flex;
  flex-direction: row;
  padding: 0 1em;
  align-items: center;
  justify-content: flex-end;
  gap: 1em;
  font-size: 1.2rem;
}

.ressource-container > a {
  color: white;
}

.ressource-container > a:is(:hover, :focus) {
  color: #00b3c4;
}
